import { Component, OnInit } from '@angular/core';
import { AssetService } from 'src/app/shared/asset.service';
import { Asset } from 'src/app/shared/asset.model';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-asset-list',
  templateUrl: './asset-list.component.html',
  styleUrls: ['./asset-list.component.css']
})
export class AssetListComponent implements OnInit {
  pageOfItems: Array<any>;
  constructor(public service: AssetService, private toastr: ToastrService) { }

  ngOnInit() {
    this.service.refreshList();
  }
  onChangePage(pageOfItems: Array<any>) {
    // update current page of items
    this.pageOfItems = pageOfItems;
  }
  populateForm(ast: Asset) {
    this.service.formData = Object.assign({}, ast);
  }
  onDelete(id: number) {
    if (confirm('Are you sure you want to delete this record?')) {
      this.service.deleteAsset(id).subscribe(res => {
        this.service.refreshList();
        this.toastr.warning('Asset has been deleted successfully', 'Deleted Successfully');
      });
    }
  }
}
