import { Injectable } from '@angular/core';
import { Asset } from './asset.model';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AssetService {
  formData: Asset;
  list: Asset[];
  readonly rootURL = 'http://localhost:62958/api/';
  constructor(private http: HttpClient) { }
  postAsset(formData: Asset) {
    return this.http.post<Asset>(this.rootURL + 'Assets', formData);
  }
  refreshList() {
    this.http.get(this.rootURL + 'Assets').toPromise().then(res => this.list = res as Asset[]);
  }
  putAsset(formData: Asset) {
    return this.http.put(this.rootURL + 'Assets/' + formData.id, formData);
  }
  deleteAsset(id: number) {
    return this.http.delete(this.rootURL + 'Assets/' + id);
  }
}
