import { Component, OnInit } from '@angular/core';
import { AssetService } from 'src/app/shared/asset.service';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-asset',
  templateUrl: './asset.component.html',
  styleUrls: ['./asset.component.css']
})
export class AssetComponent implements OnInit {

  constructor(public service: AssetService,
              private toastr: ToastrService) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm(form?: NgForm) {
    if (form != null) {
      form.resetForm();
    }
    this.service.formData = {
      asset_id: '',
      country: '',
      created_by : '',
      description: '',
      email: '',
      file_name: '',
      id: null,
      mime_type: ''
    };
  }
  onSubmit(form: NgForm) {
    console.log(form.value);
    if (form.value.id == null) {
      this.InsertRecord(form);
    } else {
      this.UpdateRecord(form);
    }
  }
  InsertRecord(form: NgForm) {
    this.service.postAsset(form.value).subscribe(() => {
      this.toastr.success('Asset has been added successfully', 'Insert Successfully');
      this.resetForm(form);
      this.service.refreshList();
    });
  }
  UpdateRecord(form: NgForm) {
    this.service.putAsset(form.value).subscribe(() => {
      this.toastr.info('Asset has been updated successfully', 'Updated Successfully');
      this.resetForm(form);
      this.service.refreshList();
    });
  }
}
